@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700&display=swap);
.logo {
  width: 80px;
  margin: 80px 140px;
}
.burger-btn {
  display: none;
}

@media screen and (max-width: 600px) {
  .burger-btn {
    display: block;
    z-index: 1;
  }
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #ea9085;
    font-size: 30px;
    margin: 40px;
  }
  .logo {
    width: 60px;
    margin: 0px;
  }
}

.main-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto 140px;
}

.text-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.main-text {
  font-size: 160px;
  font-weight: bold;
  color: #ffa372;
  letter-spacing: 14px;
}

.sub-text {
  font-size: 48px;
  color: #ea9085;
}

@media screen and (max-width: 600px) {
  .main-container {
    margin: 100px 50px;
    justify-content: center;
    text-align: center;
  }
  .main-text {
    font-size: 60px;
    margin-bottom: 30px;
    letter-spacing: 8px;
  }

  .sub-text {
    font-size: 30px;
  }
}

.nav-container {
  color: #512b58;
  font-size: 30px;
}

li {
  list-style: none;
  margin: 20px auto;
}
li:hover {
  cursor: pointer;
  translate: 5px;
  transition: all 0.2s;
}

a {
  text-decoration: none;
  color: #512b58;
}

a:hover {
  color: #ea9085;
}

@media screen and (max-width: 600px) {
  .nav-container {
    display: none;
  }
}

.projects-container {
  overflow: hidden;
}

.projects-title {
  text-align: center;
  font-size: 48px;
  color: #ea9085;
  margin-bottom: 100px;
}
.cards-end {
  text-align: center;
  margin: 100px auto;
}

.cards-end-title {
  color: #ffa372;
  font-size: 28px;
  margin-bottom: 16px;
}
.cards-end-text {
  font-size: 22px;
  color: #ffffff;
}

.cards-end-link {
  color: #ea9085;
}

@media screen and (max-width: 600px) {
  .projects-title {
    font-size: 38px;
    margin-bottom: 80px;
  }

  .cards-end-title {
    font-size: 22px;
    margin-bottom: 12px;
  }
  .cards-end-text {
    font-size: 18px;
  }
}

.card-container {
  display: flex;
  background: #ea9085;
  align-items: center;
  justify-content: space-between;
  margin: 50px 15%;
  border-radius: 5px;
  padding: 50px 30px;
}
.left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 65%;
}
.right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 30%;
}
.card-title {
  color: #343336;
  font-size: 40px;
  margin-bottom: 20px;
}
.card-desc {
  font-size: 22px;
  color: #343336;
}
.language-container {
  margin-bottom: 20px;
}

.languages {
  color: #512b58;
  font-size: 22px;
}
.language-logo-container {
  font-size: 80px;
}

@media screen and (max-width: 600px) {
  .card-container {
    flex-direction: column;
    margin: 50px 10%;
    padding: 30px 10px;
  }
  .left {
    display: flex;
    align-items: flex-start;
    width: 90%;
  }
  .right {
    text-align: center;
    width: 90%;
    margin-top: 40px;
  }
  .card-title {
    font-size: 30px;
    font-weight: bold;
  }

  .card-desc {
    font-size: 18px;
  }

  .languages {
    color: #512b58;
    font-size: 18px;
  }
  .language-logo-container {
    font-size: 60px;
  }
}

.button-container {
  width: 200px;
  height: 50px;
  background-color: #512b58;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 2px;
}

.button-title {
  color: #ffffff;
  font-size: 22px;
  text-align: center;
  transition: all 0.2s ease-in-out;
}

.button-title:hover {
  color: #ea9085;
}

@media screen and (max-width: 600px) {
  .button-container {
    height: 38px;
  }

  .button-title {
    font-size: 18px;
  }
}

.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto 100px;
}
.about-hero-container {
  display: flex;
  justify-content: center;
  font-size: 28px;
  color: #ffffff;
  overflow: hidden;
  /* margin-bottom: 50px; */
}
.about-hero-title {
  font-size: 40px;
  color: #ea9085;
  text-align: center;
  /* margin-bottom: 50px; */
}
.about-hero-text {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
}

.about-image-container {
  margin-left: 100px;
}

.img {
  max-width: 100%;
  height: auto;
}

.skills-card-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
}

.skills-title {
  color: #ea9085;
  font-size: 40px;
  margin: 100px auto 50px auto;
}
.skills-content-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.skills {
  font-size: 28px;
  color: #ffffff;
  margin: auto 60px;
}

@media screen and (max-width: 600px) {
  .about-container {
    margin: auto auto;
    overflow: hidden;
  }

  .about-hero-container {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    overflow: hidden;
  }
  .about-hero-title {
    font-size: 24px;
    margin-bottom: 28px;
  }
  .about-hero-text {
    justify-content: center;
    align-items: center;
    width: 90%;
  }

  .about-image-container {
    margin: auto;
  }

  .skills-card-container {
    margin-bottom: 100px;
  }

  .skills-title {
    color: #ea9085;
    font-size: 30px;
    margin: 80px auto 28px auto;
  }
  .skills-content-container {
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
  }
  .skills {
    font-size: 18px;
    margin: auto 10px;
  }
}

.contact-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.contact-title {
  font-size: 40px;
  color: #ea9085;
  margin-bottom: 50px;
}
.contact-box {
  background-color: #ea9085;
  border-radius: 20px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-item {
  color: #ffffff;
  font-size: 40px;
  margin: auto 10px;
}

.contact-item a {
  color: #512b58;
}
.contact-item a:hover {
  color: #ffffff;
}

@media screen and (max-width: 600px) {
  .contact-container {
    width: 100%;
  }
  .contact-title {
    font-size: 30px;
    margin: 50px auto;
  }
}

.mobile-nav {
  display: none;
}

@media screen and (max-width: 600px) {
  .mobile-nav {
    position: absolute;
    display: flex;
    background-color: #ea9085;
    width: 100%;
    height: 60vh;
    top: 0;
    align-items: center;
    justify-content: center;
    z-index: 1;
  }
  .mobile-nav-links {
    display: flex;
    flex-direction: column;
    color: #512b58;
    font-size: 22px;
  }
}

* {
  font-family: "Titillium Web", sans-serif;
  box-sizing: border-box;
  margin: 0%;
  padding: 0%;
}

body {
  background-color: #2c003e;
}

